/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 20px;
}

.alert-errors {
  color: #842029;
  background: #f8d7da;
}

.alert-success {
  color: #0f5132;
  background: #d1e7dd;
}

/* forms */
.form {
  width: 90vw;
  max-width: 600px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 2rem 2.5rem;
  margin: 3rem auto;
}

.form-label {
  display: block;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 1px;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: 20px;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
}

.form-control {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}

::placeholder {
  font-family: inherit;
  color: #94a3b8;
}

.form-alert {
  color: #842029;
  letter-spacing: 1px;
  text-transform: capitalize;
}

/* titles */
.title {
  margin: 1.75rem;
  text-align: center;
}

.title-underline {
  background: #645cff;
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: 1rem;
}

/* images */
.imgContainer {
  width: 40vw;
  max-width: 1120px;
  border-radius: 20px;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.container {
  width: 90%;
  max-width: 1120px;
}

.card {
  display: block;
  padding: 0.75rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border: 1px solid #ddd;
  border-radius: 5px;
}
.card .card-title {
  font-size: 1.5rem;
  padding-bottom: 0.75rem;
}
.card .card-body {
  font-size: 1rem;
}
.card .card-body a {
  text-decoration: underline;
}
.card .btn-test {
  background-color: #9900ff;
}

.header {
  width: 100%;
  min-height: 100vh;
  background-color: #e2e8f0;
  display: grid;
  place-items: center;
}

.main {
  display: grid;
  place-items: center;
}

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}